/* eslint-disable array-callback-return */
import React, { useEffect, useState } from 'react'
import { useHistory } from 'react-router-dom';
import './index.css';


function Home(props) {
    const [list, setlist] = useState([]);
    const [saved_words, setsaved_words] = useState([]);
    const [ml, setml] = useState([]);
    const [txt, settxt] = useState('');
    const [tip, settip] = useState('(put output here)');
    const [output_description, seoutput_description] = useState('(fill in the description)');
    
    function showdata() {
        if (localStorage.getItem('saved_words') != null) {
            setsaved_words(JSON.parse(localStorage.getItem('saved_words')))
            // document.querySelector('#saved_words').innerHTML = saved_words.join(',')
        }
    }
    useEffect((props) => {
        showdata();
        document.addEventListener('keydown', function (event) {

            if (event.keyCode === '13') {
                // var txt = document.querySelector('#word_input').value;
                if (txt !== '') {
                    search(txt, 1)
                }
            }
        })
    }, []);
    function savebtn(index) {
        console.log(index)
        let result = saved_words;
        result.push(list[index].word)
        console.log(result)
        setsaved_words([...result])
        localStorage.setItem('saved_words', JSON.stringify(saved_words))
        alert('success');
        
    }
    function search(txt, type) {
        settip('â€¦loading')
        var url = ' ';
        if (type === 1) {
            url = `https://api.datamuse.com/words?rel_rhy=${txt}`;
            seoutput_description(`Words that rhyme with ${txt}</br>1 syllable`)
        } else {
            url = `https://api.datamuse.com/words?ml=${txt}`;
            seoutput_description(`Words with a similar meaning to ${txt}`)
        }
        fetch(url, {
            method: 'GET',
            headers: {
                'content-type': 'application/x-www-form-urlencoded'
            }
        }).then(response => response.json())
            .then((res) => {
                settip('')
                console.log(res)
                setlist(res);
            }).catch((err) => {
                console.log(err)
            })
    }
    return (
        <div id="content">
            <main className="container">

                <h1 className="row">Rhyme Finder (579 Problem Set 5)</h1>
                <div className="row">
                    <div className="col">Saved words: <span id="saved_words">{saved_words.join(',')}
                    </span>
                    </div>
                </div>
                <div className="row">
                    <div className="input-group col">
                        <input className="form-control" type="text" placeholder="Enter a word" id="word_input" onChange={(e) => {
                            settxt(e.target.value)
                        }} />
                        <button id="show_rhymes" type="button" className="btn btn-primary"
                            onClick={() => {
                                if (txt !== '') {
                                    search(txt, 1)
                                }
                            }}
                        >Show rhyming words</button>
                        <button id="show_synonyms" type="button" className="btn btn-secondary"
                            onClick={() => {
                                if (txt !== '') {
                                    search(txt, 2)
                                }
                            }}
                        >Show synonyms</button>
                    </div>
                </div>
                <div className="row">
                    <h2 className="col" id="output_description">(fill in the description)</h2>
                </div>
                <div className="output row">
                    <output id="word_output" className="col">
                        {tip}
                        {
                            list.length === 0 && txt !== "" ? "no data" :
                                list.map((ele, i) => {
                                    return (
                                        <li key={i}>{ele.word}show_rhymes
                                            <button className='save btn btn-outline-success' onClick={() => { savebtn(i) }}>save</button>
                                        </li>
                                    )
                                })
                        }
                    </output>
                </div>
            </main>
            <footer><a href='https://github.com/shuangli0104/ShuangL_Problem_Set_6'>https://github.com/shuangli0104/ShuangL_Problem_Set_6</a></footer>
        </div >

    )
}

export default Home

